$spacer: 1rem;
$spacers: (
  0: 0,
  1: $spacer * 0.25,
  2: $spacer * 0.5,
  3: $spacer,
  4: $spacer * 1.5,
  5: $spacer * 3,
  6: ($spacer * 5),
  7: ($spacer * 8),
  8: ($spacer * 10),
  9: ($spacer * 11),
  10: ($spacer * 14),
  11: ($spacer * 16),
  12: ($spacer * 20),
  'sm': ($spacer * 1),
  'md': ($spacer * 2),
  'lg': ($spacer * 4),
  'xl': ($spacer * 8)
);

$negative-spacers: if($enable-negative-margins, negativify-map($spacers), null);

