/**
 * = Modals
 */
$modal-content-border-radius: 8px;

.modal {
  &.static-example {
    position: relative;
    display: block;
  }
}

.modal-content {
  border: 0;
  background: #F5FBFA;

  @if $enable-rounded {
    border-radius: $modal-content-border-radius;
  }

  @else {
    border-radius: 0;
  }
}

.modal-fluid {
  .modal-dialog {
    margin-top: 0;
    margin-bottom: 0;
  }

  .modal-content {
    border-radius: 0;
  }
}

@each $color,
$value in $theme-colors {
  .modal-#{$color} {
    @include modal-variant($value);
  }
}

@include media-breakpoint-up(sm) {}

.btn-close-absolute {
  position: absolute;
  right: 10px;
  top: 10px;
}

.timeline {
  .number {
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    line-height: 32px;
    color: #FFFFFF;
    width: 32px;
    height: 32px;
    text-align: center;
    border-radius: 50%;
    background: $primary-gradient;
    outline: 2px solid $primary;
    outline-offset: 2px;
    -moz-outline-radius: 50%;

    &.bg-gray {
      background: #B0B0B0;
      outline: 2px solid #B0B0B0;
    }

    &.bg-success {
      background: #22A09D;
      outline: 2px solid #22A09D;
    }
  }
}