.sub_chart {
  .stat {
    h2 {
      font-weight: 400;
      color: rgba(0, 0, 0, 0.85);
      font-size: 13px;
      line-height: 20px;

      @include media-breakpoint-up(sm) {
        font-size: 13px;
        line-height: 20px;
      }

      @include media-breakpoint-up(md) {
        font-size: 14px;
        line-height: 22px;
      }

      @include media-breakpoint-up(lg) {
        font-size: 14px;
        line-height: 22px;
      }

      @include media-breakpoint-up(xl) {}

      @include media-breakpoint-up(xxl) {}
    }

    h3 {
      font-weight: 600;
      color: rgba(0, 0, 0, 0.85);
      font-size: 24px;
      line-height: 30px;

      @include media-breakpoint-up(sm) {
        font-size: 26px;
        line-height: 32px;
      }

      @include media-breakpoint-up(md) {
        font-size: 30px;
        line-height: 34px;
      }

      @include media-breakpoint-up(lg) {
        font-size: 34px;
        line-height: 36px;
      }

      @include media-breakpoint-up(xl) {}

      @include media-breakpoint-up(xxl) {}
    }
  }
}