/**
 * = Cards
 */

.card {
  position: relative;
  background: #FFFFFF;
  box-shadow: 0px 3px 20px rgba(0, 0, 11, 0.1);
  border-radius: 4px;
  box-shadow: 0px 3px 20px rgba(0, 0, 11, 0.1);
  border-radius: 8px;
  border: none;

  .card-header {
    background: transparent;
    border-bottom: 1px solid #EAEAEA;
  }

  &.hover-state {
    &:hover {
      background-color: $soft;
    }
  }

  .profile-cover {
    @include background-image(no-repeat, cover);
    height: 200px;
  }
}


.card-footer {
  background-color: transparent;
}

.card-stats {
  padding-left: 1.9rem;
}

.figure.card {

  img.svg-icon {}

  .figure-sm-title {
    font-weight: 350;
    font-size: 11px;
    line-height: 12px;
    color: rgba(60, 60, 60, 0.85);
    margin-bottom: 0;
  }

  .figure-title {
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.6px;
    color: #3C3C3C;
    margin-bottom: 0;
  }
}


.d-figure {
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 4px;

  .figure-sm-title {
    font-weight: 350;
    font-size: 11px;
    line-height: 12px;
    color: rgba(60, 60, 60, 0.85);
    margin-bottom: 0;
  }

  .figure-title {
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.6px;
    color: #3C3C3C;
    margin-bottom: 0;
  }
}

// Card with blockquotes

.card {
  .card-blockquote {
    position: relative;
    padding: 2rem;

    .svg-bg {
      display: block;
      position: absolute;
      width: 100%;
      height: 95px;
      top: -94px;
      left: 0;
    }
  }
}

// Animated cards


.page-preview {

  display: block;
  position: relative;

  .show-on-hover {
    position: absolute;
    bottom: -0;
    background: rgba($dark, .85);
    padding: 10px 0;
    width: 100%;
    left: 0;
    opacity: 0;
    @include transition(.2s);
  }

  &:hover {
    z-index: 1;

    .show-on-hover {
      opacity: 1;
    }
  }
}

.theme-settings {
  z-index: 99;
  position: fixed;
  right: 15px;
  bottom: 0;

  &,
  .card-body {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
}

.theme-settings-close {
  position: absolute;
  top: 8px;
  right: 8px;
  font-size: .65rem;
}

.btn-close {

  &.theme-settings-close {
    background: transparent escape-svg($btn-close-bg-white) center / $btn-close-width auto no-repeat; // include transparent for button elements
    border: 0; // for button elements
    @include border-radius();
    opacity: $btn-close-opacity;
  }
}

.theme-settings-expand {
  right: 15px;
  bottom: 0;
  height: 0;
  @include transition(.3s height);

  &.show {
    height: 40px;
  }

  &:hover {
    background: $gray-600;
    cursor: pointer;
  }
}