/*
 * Sidebar
*/

aside.main-sidenav {
  max-height: 100%;
  overflow-y: auto;
  width: 100%;
  transition: width .2s ease-in-out;

  @include media-breakpoint-up(md) {
    width: 210px;
  }
}

.sidebar {
  display: block;
  z-index: 100;

  @include media-breakpoint-up(md) {}

  .nav {
    white-space: nowrap;
  }

  .nav-link+.multi-level {
    margin-top: .2rem;
  }

  .nav-item {
    .nav-link {
      color: #1B4785;
    }

    &.active {
      &>.nav-link {
        color: #fff;
        background-color: $primary;
        // border-radius: 4px;
        border-left: 4px solid $secondary;

        &:hover,
        &:active,
        &:focus {
          background-color: darken($primary, 10%);
        }
      }
    }

    .nav-link {
      color: $gray-900;
      cursor: pointer;

      font-size: $font-size-base;
      vertical-align: middle;
      padding: 0.55rem 0.75rem;
      @include border-radius($border-radius);

      .link-arrow {
        font-size: $font-size-sm;
      }

      &[data-bs-toggle=collapse][aria-expanded=true] .link-arrow {
        @include transform(rotate(90deg));
        transition: $transition-base;
      }

      &:hover,
      &:focus {
        color: $gray-100;
        background-color: darken($primary, 10%);

        .sidebar-icon {
          svg {
            fill: $white;

            path {
              fill: $white;
            }
          }
        }
      }
    }

    margin-bottom: .2rem;

    // SVG Icon
    .sidebar-icon {
      margin-right: .5rem;
      color: $gray-400;

      svg {
        fill: $primary;

        path {
          fill: $primary;
        }
      }
    }

    &.active {
      .sidebar-icon {
        svg {
          fill: $white;

          path {
            fill: $white;
          }
        }
      }
    }
  }

  .multi-level {
    .nav-link {
      padding-left: 53px;
    }
  }

  .sidebar-text,
  .link-arrow,
  .badge,
  .notification-count {
    opacity: 1;
  }
}

.sidebar-inner {
  position: relative;
  overflow-y: hidden;
}

.sidebar-heading {
  font-size: .75rem;
  text-transform: uppercase;
}

.user-card {
  border-bottom: 0.0625rem solid #2e3650;
}

@include media-breakpoint-down(sm) {
  .sidebar {
    width: 100%;
  }
}

.content {
  width: 100%;
  overflow: hidden;
  padding: 0 $spacer 0 $spacer;
  background: #FFFFFF;
  @include transition(all .3s);

  @include media-breakpoint-up(lg) {
    // margin-left: 260px;
    width: calc(100% - 215px);
  }
}

.sidebar-toggle {
  font-size: 1.3rem;

  &:hover,
  &:focus {
    outline: none;
    box-shadow: none;
    background: $gray-300;
  }

}