 //Tabs 

 .nav-tabs {
   .nav-item {
     margin-bottom: 0;
   }

   .nav-link {
     border: 0;
     padding: 1rem 1rem;
     background-color: $white;

     &.active {
       color: $white;
       background-color: $primary;
     }
   }

   &.nav-dark {
     .nav-link {
       &.active {
         color: $white;
         background-color: #12358a;
       }
     }
   }

   &.nav-light {
     .nav-link {
       &.active {
         color: $black;
         background-color: $light;
       }

       &:hover {
         color: $black;
       }
     }
   }
 }

 // Pills 

 .nav-pills:not(.mobile-sm) {

   .nav-item:not(:last-child) {
     padding-right: $nav-pills-space-x;
     flex-basis: 33%;

     // Small devices (landscape phones, 576px and up)
     @include media-breakpoint-up(sm) {
       flex-basis: 33%;
     }

     // Medium devices (tablets, 768px and up)
     @include media-breakpoint-up(md) {
       flex-basis: 50%;
     }

     // Large devices (desktops, 992px and up)
     @include media-breakpoint-up(lg) {
       flex-basis: 0;
     }

     // X-Large devices (large desktops, 1200px and up)
     @include media-breakpoint-up(xl) {
       flex-basis: 0;
     }

     // XX-Large devices (larger desktops, 1400px and up)
     @include media-breakpoint-up(xxl) {
       flex-basis: 0;
     }
   }

   .nav-link {
     transition: $transition-base;
     box-shadow: $nav-link-shadow;
     border: $nav-pills-border-width solid $nav-pills-border-color;
     padding: ($nav-pills-padding-y / 2.5) ($nav-pills-padding-x / 4);

     // Small devices (landscape phones, 576px and up)
     @include media-breakpoint-up(sm) {
       font-size: 13px;
       padding: ($nav-pills-padding-y / 2.5) ($nav-pills-padding-x / 3);
     }

     // Medium devices (tablets, 768px and up)
     @include media-breakpoint-up(md) {
       font-size: 14px;
       padding: ($nav-pills-padding-y / 2) ($nav-pills-padding-x / 2);
     }

     // Large devices (desktops, 992px and up)
     @include media-breakpoint-up(lg) {
       font-size: 14px;
       padding: ($nav-pills-padding-y / 2) $nav-pills-padding-x;
     }

     // X-Large devices (large desktops, 1200px and up)
     @include media-breakpoint-up(xl) {
       font-size: 14px;
       padding: ($nav-pills-padding-y / 1) $nav-pills-padding-x;
     }

     // XX-Large devices (larger desktops, 1400px and up)
     @include media-breakpoint-up(xxl) {
       font-size: 14px;
       padding: ($nav-pills-padding-y / 1) $nav-pills-padding-x;
     }

     &.avatar-link {
       border: 0;
     }

     &:hover {
       color: $nav-pills-link-hover-color;
     }
   }

   .nav-link.active,
   .show>.nav-link {
     color: $nav-pills-link-active-color;
     background-color: $nav-pills-link-active-bg;
     border-color: $nav-pills-link-active-color;
   }

   &.rounded {
     .nav-link {
       border-radius: 30px;
     }

     &.vertical-tab {
       .nav-link {
         margin-bottom: .625rem;
         min-width: 100px;
       }

       .nav-item:not(:last-child) {
         padding-right: 0;
       }
     }
   }

   &.bordered-pill-md {
     .nav-link {
       border: $border-width-md solid $gray-200;
       font-weight: $font-weight-bold;
     }
   }

   &.vertical-tab {
     .nav-link {
       margin-bottom: .625rem;
     }

     .nav-item:not(:last-child) {
       padding-right: 0;
     }
   }

   @include media-breakpoint-down(xs) {
     .nav-item {
       margin-bottom: $spacer;
     }
   }
 }

 @include media-breakpoint-down(sm) {
   .nav-pills:not(.nav-pills-circle) {
     .nav-item {
       padding-right: 0;
     }
   }
 }


 // Navs circle

 .nav-pill-circle {
   .nav-link {
     @include display-flex();
     text-align: center;
     height: 80px;
     width: 80px;
     padding: 0;
     box-shadow: $nav-link-shadow;
     border-radius: $nav-pills-border-circle;
     align-items: center;
     justify-content: center;

     &.avatar-link {
       box-shadow: none;
     }
   }

   &.vertical-tab {
     .nav-link-icon {

       i,
       svg {
         font-size: 1.5rem;
       }
     }

     .nav-item:not(:last-child) {
       padding-right: 0;
     }
   }

   .nav-link-icon {

     i,
     svg {
       font-size: 1.25rem;
       margin: 0;
       display: block;
     }
   }
 }

 // Navs square

 .nav-pill-square {
   .nav-link {
     text-align: center;
     min-width: 80px;
     box-shadow: $nav-link-shadow;
     @include display-flex();
     align-items: center;
     justify-content: center;
   }

   &.vertical-tab {
     .nav-link {
       margin-bottom: .625rem;
       min-width: 100px;
     }

     .nav-item:not(:last-child) {
       padding-right: 0;
     }
   }

   .nav-link-icon {

     i,
     svg {
       font-size: 1.25rem;
       margin: 0;
       display: block;
       line-height: 50px;
     }
   }
 }

 // Nav wrapper 
 .nav-wrapper {
   padding: 1rem 0;
   @include border-top-radius($card-border-radius);

   +.card {
     @include border-top-radius(0);
     @include border-bottom-radius($card-border-radius);
   }
 }

 // Tabbable tabs
 //
 // Hide tabbable panes to start, show them when `.active`

 .tab-content {
   >.tab-pane {
     display: none;

     pre {
       padding: 0;
       margin: 0;
     }
   }

   >.active {
     display: block;
   }
 }

 .nav-pills .nav-item {
   @include media-breakpoint-up(lg) {
     margin-bottom: 0;
   }
 }

 // TABS 
 .navtabs-wrapper {
   .nav {
     border-bottom: 1px solid #EAEAEA;

     .nav-item {
       .nav-item:not(:last-child) {
         padding-right: 0;
       }

       .nav-link {
         font-style: normal;
         font-weight: 400;
         // font-size: 12px;
         // line-height: 22px;
         color: #707070;
         border: 1px solid #fff;
         border-radius: 4px 4px 0px 0px;

         // Small devices (landscape phones, 576px and up)
         // @include media-breakpoint-up(sm) {
         //   font-size: 13px; 
         // }

         // // Medium devices (tablets, 768px and up)
         // @include media-breakpoint-up(md) {
         //   font-size: 14px; 
         // }

         // // Large devices (desktops, 992px and up)
         // @include media-breakpoint-up(lg) {
         //   font-size: 14px; 
         // }

         // // X-Large devices (large desktops, 1200px and up)
         // @include media-breakpoint-up(xl) {
         //   font-size: 14px; 
         // }

         // // XX-Large devices (larger desktops, 1400px and up)
         // @include media-breakpoint-up(xxl) {
         //   font-size: 14px; 
         // }

         &.active {
           background: rgba($success, 0.07);
           border: 1px solid $success;
           color: $success;
         }
       }
     }
   }

   .tab-content {
     margin-top: 25px;
   }

   &.style-2 {
     .nav {
       border-bottom: 1px solid #ffffff;

       .nav-item {
         .nav-link {
           font-style: normal;
           font-weight: 700;
           font-size: 16px;
           line-height: 22px;
           color: #707070;
           border: none;
           border-bottom: 2px solid #fff;
           border-radius: 4px 4px 0px 0px;

           &.active {
             background: rgba($success, 0.01);
             border-bottom: 2px solid $success;
             color: #242424;
           }
         }
       }
     }

     &.style-3 {
       .nav {
         border-bottom: 1px solid #D9D9D9;

         .nav-item {
           .nav-link {
             color: #707070;
             border: 1px solid #fff;
             border-bottom: 2px solid #fff;
             border-radius: 4px 4px 0px 0px;
             min-width: 75px;

             &.active {
               background: rgba($success, 0.1);
               border: 1px solid $success;
               border-bottom: 2px solid $success;
               color: $success;
             }
           }
         }
       }
     }
   }
 }