.bg-light {
  background: #F5FBFA;
}

.bg-preview {
  background: #E7F5F4;
}

.bg-modals {
  background: #F1F5F8;
}

.mw-100px {
  min-width: 100px;
}

// write your custom styles here!
.card.integration {
  img.svg-icon {
    width: 90px;
    height: 90px;
  }

  .title {}

  .text {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #707070;
  }
}

.dropdown-menu-end {
  right: 0 !important;
}

.step-wrapper {
  z-index: 1;

  img.main-img {
    z-index: 2;
  }

  .text-wrapper {
    top: -20px;
    z-index: 4;

    .text {
      top: 8px;
    }
  }
}

.loader {
  animation: rotate reverse 3s ease-in infinite;
}

@keyframes rotate {
  to {
    transform: rotate(0deg);
    filter: hue-rotate(0deg);
  }

  from {
    transform: rotate(360deg);
    filter: hue-rotate(360deg);
  }
}