.form-control,
.form-select {

  padding: $padding-xs;
  font-size: $fs-xs;

  // Small devices (landscape phones, 576px and up)
  @include media-breakpoint-up(sm) {
    padding: $padding-sm;
    font-size: $fs-sm;
  }

  // Medium devices (tablets, 768px and up)
  @include media-breakpoint-up(md) {
    padding: $padding-md;
    font-size: $fs-md;
  }

  // Large devices (desktops, 992px and up)
  @include media-breakpoint-up(lg) {
    padding: $padding-lg;
    font-size: $fs-lg;
  }

  // X-Large devices (large desktops, 1200px and up)
  @include media-breakpoint-up(xl) {
    padding: $padding-xl;
    font-size: $fs-xl;
  }

  // XX-Large devices (larger desktops, 1400px and up)
  @include media-breakpoint-up(xxl) {
    padding: $padding-xxl;
    font-size: $fs-xl;
  }

  // border: 1px solid #D7D7D7;
  border-color: #D7D7D7;
  border-radius: 4px;
  font-style: normal;
  font-weight: 400;
  // font-size: 14px;
  // line-height: 22px;
  color: #1d1d1d;

  &:focus,
  &:active,
  &:hover {
    color: #1d1d1d;
  }

  @include placeholder {
    color: #606060;
  }

  &-sm {
    padding: $padding-xxs;
    font-size: $fs-xxs;

    // Small devices (landscape phones, 576px and up)
    @include media-breakpoint-up(sm) {
      padding: $padding-xs;
      font-size: $fs-xxs;
    }

    // Medium devices (tablets, 768px and up)
    @include media-breakpoint-up(md) {
      padding: $padding-sm;
      font-size: $fs-xs;
    }

    // Large devices (desktops, 992px and up)
    @include media-breakpoint-up(lg) {
      padding: $padding-md;
      font-size: $fs-sm;
    }

    // X-Large devices (large desktops, 1200px and up)
    @include media-breakpoint-up(xl) {
      padding: $padding-lg;
      font-size: $fs-sm;
    }

    // XX-Large devices (larger desktops, 1400px and up)
    @include media-breakpoint-up(xxl) {
      padding: $padding-xl;
      font-size: $fs-sm;
    }
  }

  &[type='file'] {
    padding: 0;
  }

  @include media-breakpoint-up(lg) {
    &.form-control-lg {
      padding: $inpu-lg-padding-y $input-padding-x;
    }
  }

  @include media-breakpoint-up(lg) {
    &.form-control-lg {
      padding: $inpu-lg-padding-y $input-padding-x;
    }
  }
}

.search-bar {
  .form-control {
    width: 280px;
    transition: width .2s ease-in-out;

    @include media-breakpoint-down(sm) {
      width: 120px;

      &:focus {
        width: 150px;
      }
    }
  }
}